import styles from './index.module.scss';
import Button from '../../../../share/components/Button';
import { motion } from "framer-motion";
import { educationListInfo } from '../../consts';

const listAnimationConfig = {
  visible: i => ({
    opacity: 1,
    x: 0,
    transition: {
      delay: i * 0.5,

    },
  }),
  hidden: { opacity: 0, x:100 },
};

const Greeting = () => {
  return (
    <section className={styles.greeting}>
      <h2 className={styles.greeting__title}> </h2>
      <h2 className={styles.greeting__title}>front-end </h2>
      <Button text={"  "} path={"contacts"}></Button>
      <h3 className={styles.greeting__subtitle}> </h3>
      <ul className={styles["greeting__experience-list"]}>
        <li className={styles["greeting__experience-item"]}>
          JS full stack developer   REON c 04.09.2022 
          10.09.2023
        </li>
        <p>
                amoCRM.  
              ,   , 
          express  mongoDB,       
          ,    ,  React + Redux 
          Vue 3.
               EPAM    frontend .
        </p>
      </ul>
      <h3 className={styles.greeting__subtitle}></h3>
      <ul className={styles["greeting__education-list"]}>
        {educationListInfo.map((text, index) => (
          <motion.li
            className={styles["greeting__education-item"]}
            variants={listAnimationConfig}
            initial="hidden"
            animate="visible"
            custom={index}
          >
            {text}
          </motion.li>
        ))}
      </ul>
    </section>
  );
}

export default Greeting;